}

.chat .chat-title {
    background-color: #EEEEEE;
    padding: 20px 16px;
}

.chat .chat-body {
    -ms-flex: auto;
        flex: auto;
    overflow-y: auto;
}

.chat .chat-input-wrapper {
    display: -ms-flexbox;
    display: flex;
